import { createTheme } from "@mui/material"
import { ThemeProvider } from "@mui/styles"
import { useEffect, useState } from "react"
import { BrowserRouter, Link, Route, Routes } from "react-router-dom"
import { Cart, Checkout, Navbar, Products } from "./components"
import { commerce } from "./lib/commerce"

const theme = createTheme()

const App = () => {

    const [products, setProducts] = useState([])
    const [cart, setCart] = useState({});
    const [order, setOrder] = useState({})
    const [errorMessage, setErrormessage] = useState('')

    // Fetch Products - List Products
    const fetchProducts = async () => {
        const res = await commerce.products.list()
        setProducts(res.data)
    }

    // Fetch cart
    const fetchCart = async () => {
        setCart(await commerce.cart.retrieve())
    }

    // Add to cart
    const handleAddToCart = async (productId, quantity) => {
        const res = await commerce.cart.add(productId, quantity)
        setCart(res.cart)
    }

    // Update Product Qty in cart
    const handleUpdateCartQty = async (productId, quantity) => {
        const res = await commerce.cart.update(productId, { quantity })
        setCart(res.cart)
    }

    // Remove Product from Cart
    const handleRemoveFromCart = async (productId) => {
        const res = await commerce.cart.remove(productId)
        setCart(res.cart)
    }

    // Empty Cart
    const handleEmptyCart = async () => {
        const res = await commerce.cart.empty()
        setCart(res.cart)
    }

    // refresh Cart
    const refreshCart = async () => {
        const newcart = await commerce.cart.refresh()
        setCart(newcart)
    }

    // Place order - capture API issue here, directly showing error message, not executing try block
    const handleCaptureCheckout = async (checkoutToken, newOrder) => {
        // alert('function call')
        try {
            console.log('newDataID', checkoutToken)
            console.log('newData', newOrder)
            const incomingorder = await commerce.checkout.capture(checkoutToken, newOrder)
            console.log('incomingorder', incomingorder)
            setOrder('incomingorder', incomingorder)
            refreshCart()
        } catch (error) {
            console.log('order error', error)
            setErrormessage(error.data.error.message)
        }
    }

    useEffect(() => {
        fetchProducts()
        fetchCart()
    }, [])

    // console.log('cart', cart)
    console.log('order', order)

    return (
        <BrowserRouter>
            <ThemeProvider theme={theme}>
                <Navbar totalItems={cart.total_items} />
                <Routes>
                    <Route path="/" element={<Products products={products} onAddTocart={handleAddToCart} />} />
                    <Route path="cart" element={<Cart cart={cart} handleUpdateCartQty={handleUpdateCartQty} handleRemoveFromCart={handleRemoveFromCart} handleEmptyCart={handleEmptyCart} />} />
                    <Route path="checkout" element={<Checkout cart={cart} order={order} onCaptureCheckout={handleCaptureCheckout} error={errorMessage} />} />
                </Routes>
            </ThemeProvider>
        </BrowserRouter>
    )
}

export default App
