import { Button, Container, Grid, Typography } from "@mui/material";
import { Link } from "react-router-dom";
import { CartItem } from "./CartItem/CartItem";
import { useStyle } from "./styles";

export const Cart = ({ cart,handleUpdateCartQty, handleRemoveFromCart, handleEmptyCart }) => {
  // console.log(cart);
  const classes = useStyle()

  const EmptyCart = () => (
    <Typography variant="subtitle1">
      Your have no items in your shopping cart, <Link to="/" className={classes.link}>Start adding some!!!!</Link>
    </Typography>
  );

  const FilledCart = () => (
    <>
      <Grid container spacing={3}>
        {cart.line_items &&
          cart.line_items.map((item) => {
            return (
              <Grid item xs={12} sm={4} key={item.id}>
                <CartItem item={item} onUpdateCartQty={handleUpdateCartQty} onRemoveFromCart={handleRemoveFromCart} />
              </Grid>
            );
          })}
      </Grid>
      <div className={classes.cardDetails}>
        <Typography variant="h4">
          Subtotal: {cart.subtotal.formatted_with_symbol}
        </Typography>
        <div>
          <Button
            className={classes.emptyButton}
            size="large"
            type="button"
            variant="contained"
            color="secondary"
            onClick={handleEmptyCart}
          >
            Empty cart
          </Button>
          <Button
            className={classes.checkoutButton}
            component={Link}
            to="/checkout"
            size="large"
            type="button"
            variant="contained"
            color="primary"
          >
            Checkout
          </Button>
        </div>
      </div>
    </>
  );

  if(!cart.line_items) return 'Loading....'

  return (
    <Container>
      <div className={classes.toolbar}>
        <Typography variant="h3" className={classes.title} gutterBottom>Your Shopping Cart</Typography>
        {/* {!cart.line_items && <EmptyCart />}
        {cart.line_items && <FilledCart />} */}
        {!cart.line_items.length ? <EmptyCart /> : <FilledCart />}
      </div>
    </Container>
  );
};
