import { AddShoppingCart } from "@mui/icons-material";
import {
  Card,
  CardActions,
  CardContent,
  CardMedia,
  IconButton,
  Typography,
} from "@mui/material";
import { useStyle } from "./styles";


export const Product = ({ product, onAddTocart }) => {

    const classes = useStyle()
    // console.log('products', product)

  return (
    <Card className={classes.root}>
      <CardMedia
        className={classes.media}
        image={product.image.url}
        alt={product.name}
        title={product.name}
      />
      <CardContent>
        <div className={classes.cardContent}>
          <Typography variant="h6" gutterBottom>
            {product.name}
          </Typography>
          <Typography variant="h6">{product.price.formatted_with_symbol}</Typography>
        </div>
        {/* <Typography variant="body2" color="textSecondary">
          {product.description}
        </Typography> */}
         <Typography dangerouslySetInnerHTML={{ __html: `${product.description.substring(0,20)}.....` }} variant="body2" color="textSecondary" />
      </CardContent>
      <CardActions disableSpacing className={classes.cardActions}>
        <IconButton aria-label="Add to Cart" onClick={() => onAddTocart(product.id, 1)}><AddShoppingCart /></IconButton>
      </CardActions>
    </Card>
  );
};
