import { Button, CircularProgress, Divider, Paper, Step, StepLabel, Stepper, Typography, CssBaseline } from "@mui/material"
import { useEffect } from "react"
import { useState } from "react"
import { Link, useNavigate } from "react-router-dom"
import { commerce } from "../../../lib/commerce"
import { AddressForm } from "../AddressForm"
import { PaymentForm } from "../PaymentForm"
import { useStyle } from "./styles"

const steps = ['Shipping address', 'Payment details']

export const Checkout = ({ cart, order, onCaptureCheckout, error }) => {
    // console.log(cart)

    const [activeStep, setActiveStep] = useState(0)
    const [checkoutToken, setCheckoutToken] = useState(null)
    const [shippingData, setShippingData] = useState({})
    const [isFinsished, setIsFinished] = useState(false)
    const classes = useStyle()

    const navigate = useNavigate();

    useEffect(() => {
        const generateToken = async () => {
            try {
                const token = await commerce.checkout.generateToken(cart.id, { type: 'cart' })
                // console.log('token', token)
                setCheckoutToken(token)
            } catch (error) {
                navigate('/')
            }
        }
        if (cart.line_items) {
            generateToken();
        }
    }, [cart])

    const nextStep = () => setActiveStep(prevStep => prevStep + 1)
    const backStep = () => setActiveStep(prevStep => prevStep - 1)

    const next = (data) => {
        // console.log('next', data)
        setShippingData(data)
        nextStep()
    }

    const timeOut = () => {
        setTimeout(() => {
            setIsFinished(true)
        }, 3000)
    }

    let Confirmation = () => order.customer ? (
        <>
            <div>
                <Typography variant="h5">Thank you for your purchase, {order.customer.firstname} {order.customer.lastname}!</Typography>
                <Divider className={classes.divider} />
                <Typography variant="subtitle2">Order ref: {order.customer_reference}</Typography>
            </div>
            <br />
            <Button component={Link} variant="outlined" type="button" to="/">Back to home</Button>
        </>
    ) : isFinsished ? (
        <>
            <div>
                <Typography variant="h5">Thank you for your purchase!</Typography>
                <Divider className={classes.divider} />
            </div>
            <br />
            <Button component={Link} variant="outlined" type="button" to="/">Back to home</Button>
        </>
    ) : (
        <>
            <div className={classes.spinner}>
                <CircularProgress />
            </div >
        </>
    )

    if (error) {
        Confirmation = () => (
            <>
                <Typography variant="h5">Error: {error}</Typography>
                <br />
                <Button component={Link} variant="outlined" type="button" to="/">Back to home</Button>
            </>
        );
    }

    const Form = () => activeStep === 0 ?
        <AddressForm checkoutToken={checkoutToken} next={next} /> :
        <PaymentForm shippingData={shippingData} checkoutToken={checkoutToken} nextStep={nextStep} backStep={backStep} onCaptureCheckout={onCaptureCheckout} timeOut={timeOut} />

    return (
        <>
            <CssBaseline />
            <div className={classes.toolbar} />
            <main className={classes.layout}>
                <Paper className={classes.paper}>
                    <Typography variant="h4" align="center">Checkout</Typography>
                    <Stepper activeStep={activeStep} className={classes.stepper}>
                        {steps.map((label) => (
                            <Step key={label}>
                                <StepLabel>{label}</StepLabel>
                            </Step>
                        ))}
                    </Stepper>
                    {activeStep === steps.length ? <Confirmation /> : checkoutToken && <Form />}
                </Paper>
            </main>
        </>
    )
}