import { Button, Grid, InputLabel, MenuItem, Select, TextField, Typography } from "@mui/material"
import { useEffect } from "react"
import { useState } from "react"
import { FormProvider, useForm } from "react-hook-form"
import { Link } from "react-router-dom"
import { commerce } from "../../lib/commerce"
import { FormInput } from "./FormInput"

export const AddressForm = ({ checkoutToken, next }) => {

    const [shippingCountries, setShippingCountries] = useState([])
    const [shippingCountry, setShippingCountry] = useState('')
    const [shippingSubDivisions, setShippingSubDivisions] = useState([])
    const [shippingSubDivision, setShippingSubDivision] = useState('')
    const [shippingOptions, setShippingOptions] = useState([])
    const [shippingOption, setShippingOption] = useState('')

    const allCountries = Object.entries(shippingCountries).map(([code, name]) => ({ id: code, label: name }))
    const allSubDivisions = Object.entries(shippingSubDivisions).map(([code, name]) => ({ id: code, label: name }))
    const options = shippingOptions.map((so) => ({ id: so.id, label: `${so.description} - (${so.price.formatted_with_symbol})` }))
    // console.log('test', shippingOptions)

    const methods = useForm()
    // console.log(methods)

    const fetchshippingCountries = async (checkoutTokenId) => {
        const res = await commerce.services.localeListCountries(checkoutTokenId)
        setShippingCountries(res.countries)
        setShippingCountry(Object.keys(res.countries)[0]) // get first object key
    }

    const fetchSubDivisions = async (countryCode) => {
        const res = await commerce.services.localeListSubdivisions(countryCode)
        setShippingSubDivisions(res.subdivisions)
        setShippingSubDivision(Object.keys(res.subdivisions)[0]) // get first object key
    }

    const fetchShippingOptions = async (checkoutTokenId, country, region = null) => {
        const opt = await commerce.checkout.getShippingOptions(checkoutTokenId, { country, region })
        setShippingOptions(opt)
        setShippingOption(opt[0].id)
    }

    useEffect(() => {
        fetchshippingCountries(checkoutToken.id)
    }, [])

    useEffect(() => {
        if (shippingCountry) fetchSubDivisions(shippingCountry)
    }, [shippingCountry])

    useEffect(() => {
        if (shippingSubDivision) fetchShippingOptions(checkoutToken.id, shippingCountry, shippingSubDivision)
    }, [shippingSubDivision])

    const onSubmit = (data) => {
        // console.log('data', data)
        next({...data, shippingCountry, shippingSubDivision, shippingOption})
    }

    return (
        <>
            <Typography variant="h6" gutterBottom>Shipping Address</Typography>
            <FormProvider {...methods}>
                <form onSubmit={methods.handleSubmit(onSubmit)}>
                    <Grid container spacing={3}>
                        <FormInput name="firstName" label="First name" />
                        <FormInput name="lastName" label="Last Name" />
                        <FormInput name="address1" label="Address" />
                        <FormInput name="email" label="Email" />
                        <FormInput name="city" label="City" />
                        <FormInput name="zip" label="ZIP / Postal code" />
                        <Grid item xs={12} sm={6}>
                            <InputLabel>Shipping Country</InputLabel>
                            <Select value={shippingCountry} fullWidth onChange={e => setShippingCountry(e.target.value)}>
                                {allCountries.map((country) => (
                                    <MenuItem key={country.id} value={country.id}>{country.label}</MenuItem>
                                ))}
                            </Select>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <InputLabel>Shipping SubDivison</InputLabel>
                            <Select value={shippingSubDivision} fullWidth onChange={e => setShippingSubDivision(e.target.value)}>
                                {allSubDivisions.map((subdiv) => (
                                    <MenuItem key={subdiv.id} value={subdiv.id}>{subdiv.label}</MenuItem>
                                ))}
                            </Select>
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <InputLabel>Shipping Options</InputLabel>
                            <Select value={shippingOption} fullWidth onChange={e => setShippingOption(e.target.value)}>
                                {options.map((opt) => (
                                    <MenuItem key={opt.id} value={opt.id}>{opt.label}</MenuItem>
                                ))}
                            </Select>
                        </Grid>
                    </Grid>
                    <br />
                    <div style={{ display: 'flex', justifyContent: 'space-between' }}>
                        <Button component={Link} to={-1}>Back to Cart</Button>
                        <Button type="submit" variant="contained" color="primary">Submit</Button>
                    </div>
                </form>
            </FormProvider>
        </>
    )
}